.blog {
    .blog__container {
        background: #ffffff;
        width: 100%;
    }

    .blog__image {
        height: 233px;
        aspect-ratio: 375/233;
        object-fit: cover;
    }

    .blog__comment {
        display: flex;
        padding: 14px 16px 16px 16px;
        flex-direction: column;
        align-items: flex-start;
        gap: 14px;
        align-self: stretch;
        background-color: white;

    }

    .blog__user {
        gap: 14px;
        padding: 0 14px;
    }

    .blog__user--inner {
        border-bottom: 1px solid #D9D9D9;
        display: flex;
        align-items: center;
        gap: 14px;
        padding: 10px 0;
    }

    .blog__name {
        color: #121212;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: var(--font-family-Font-1, "Inter Display");
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: var(--line-height-15, 15px);
        /* 107.143% */
    }

    .blog__left {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    .blog__avatar {
        width: 24px;
        height: 24px;
        aspect-ratio: 1/1;
        border-radius: 30px;
        border: 0.6px solid rgba(0, 0, 0, 0.10);
    }

    .small__info {
        display: flex;
        align-items: center;
        gap: 6px;
    }

    .blog__verify {
        width: 12px;
        height: 12px;
        aspect-ratio: 1/1;
    }

    .blog__cat {
        color: #B72424;
        font-family: var(--font-family-Font-1, "Inter Display");
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: var(--line-height-15, 15px);
        /* 107.143% */
    }

    .blog__body {
        display: flex;
        padding: 0 16px;
        flex-direction: column;
        justify-content: center;
        align-items: left;
        gap: 16px;
        align-self: stretch;
        background: white;
    }
}


.blog-title {
    width: 100%;
    color: #121212;
    font-family: var(--font-family-Font-1, "Inter Display");
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 145%;
    /* 26.1px */
    letter-spacing: -0.18px;
}

.blog-sapo {
    width: 100%;
    color: #121212;
    font-family: "Inter Display";
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: 155%;
    /* 23.25px */
}

.blog-content {
    width: 100%;
    color: #393937;
    font-family: "Inter Display";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 155%;
    /* 23.25px */
}

.blog-heading {
    width: 100%;
    color: #000;
    font-family: "Inter Display";
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: 155%;
    /* 23.25px */
}

.blog-quote {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 14px;
    align-self: stretch;

    .red-line {
        width: 4px;
        align-self: stretch;
        background: #B72424;
    }

    .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 11px;
        flex: 1 0 0;

        .quote {
            color: #393937;
            font-family: "Inter Display";
            font-size: 15px;
            font-style: normal;
            font-weight: 600;
            line-height: 155%;
            /* 23.25px */
        }

        .name {
            color: #6F6F6F;
            font-family: "Inter Display";
            font-size: 14px;
            font-style: normal;
            font-weight: 600;
            line-height: 155%;
            /* 21.7px */
        }
    }
}

.comment-input {
    width: 100%;
    display: flex;
    padding: 12px 11px;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    border-radius: 10px;
    border: 1px solid #F6F6F6;
    background: #FDFDFD;

    &__placeholder {
        color: #3A3A3A;
        font-family: var(--font-family-Font-1, "Inter Display");
        font-size: var(--line-height-12, 12px);
        font-style: normal;
        font-weight: 500;
        line-height: var(--line-height-15, 15px);
        /* 125% */
    }

    &__submit {
        display: flex;
        width: 14px;
        height: 14px;
        justify-content: center;
        align-items: center;
        aspect-ratio: 1/1;
    }
}

.comments {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 10px;
    align-self: stretch;

    .avatar {
        width: 32px;
        height: 32px;
        border-radius: 32px;
    }

    .right {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
        flex: 1 0 0;

        .comment-container {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 10px;
            align-self: stretch;

            .comment {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 6px;
                align-self: stretch;
                border-radius: 12px;
                background: #F6F6F6;
                padding: 10px;


                .name {
                    color: var(--Gray-900, #1D2026);
                    text-align: right;
                    font-family: "Inter Display";
                    font-size: 13px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 20px;
                    /* 153.846% */
                    letter-spacing: -0.13px;
                }

                .content {
                    color: #4E5566;
                    font-family: "Inter Display";
                    font-size: 13px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 22px;
                    /* 169.231% */
                    letter-spacing: -0.13px;
                }
            }

            .time {
                color: var(--Gray-600, #6E7485);
                font-family: "Inter Display";
                font-size: 12px;
                font-style: normal;
                font-weight: 400;
                line-height: 16px;
                /* 133.333% */
            }
        }
    }
}